import React from 'react';
export default class MySpinner extends React.Component {

    render() {
        return (
            <div>
                {this.props.mySpinner===true ? <div className="spinnerDiv" >
                    <i className="fa fa-spinner fa-5x spinner  fa-spin" aria-hidden="true"></i>
                </div> : ""}
                {this.props.mySpinner==="overlay-off"? <div>
                    <i className="fa fa-spinner fa-5x spinner  fa-spin" aria-hidden="true"></i>
                </div> : ""}

                
            </div>
        );
    }
}

