import React, { useState } from 'react';
import{FormGroup,Label,Input} from 'reactstrap';
function NameFilter(props) {
    const [product, setProduct] = useState('');
   function onChange(e){
        setProduct(e.target.value)
        sessionStorage.setItem("product",e.target.value)
        console.log(product)
        if(e.target.value.length===0){
            props.anyUpdate(0,sessionStorage.getItem("depatmentId"),sessionStorage.getItem("code"),"__") 
            sessionStorage.setItem("product","__")
        }
        else props.anyUpdate(0, sessionStorage.getItem("depatmentId"),sessionStorage.getItem("code"),sessionStorage.getItem("product")) 
    }
  return (
    <div>
     <FormGroup >
        <Label for="exampleEmail" >Product Name Filter</Label>
          <Input style={{width:"90%"}} type="text" name="email" id="exampleEmail" placeholder="Search Product Name" onChange={onChange} value={product} />
      </FormGroup>
    </div>
  );
}
export default NameFilter;