import React, { Component } from 'react'
import { /* ListGroup, ListGroupItem, */ FormGroup, Label, CustomInput } from 'reactstrap';
import Axios from "axios";
import { apiSource } from "./Constants"
export default class CodeFilter extends Component {
    state = {
        data: []
    }
    componentDidMount() {
        this.getData()
    }
    getData = () => {
        Axios.get(`${apiSource}Promotions`)
            .then(response => {
                this.setState({ data: response.data })
                console.log("data", this.state.data)
            })
            .catch(error => console.log(error.response))
    }
    onChange = (e) => {
        console.log(e.target.value)
        sessionStorage.setItem("code", e.target.value)
        this.props.anyUpdate(0, sessionStorage.getItem("depatmentId"), e.target.value, sessionStorage.getItem("product"))
        sessionStorage.setItem("code", e.target.value)
    }

    render() {
        let { data } = this.state
        return (
            <div >
                <FormGroup>
                    <Label for="exampleCheckbox">Promotion Code Filter</Label>
                    <div>
                        <CustomInput key="afrist" type="radio" id="secondid" name="radio1" label="All" onChange={this.onChange} value="a" />

                        {

                            data.map(item => <>
                                {item.active === 0 ? <div key={"a"+item.promotionId}></div> : <CustomInput key={"a"+item.promotionId} type="radio" id={"a" + item.promotionId} name="radio1" label={item.code} onChange={this.onChange} value={item.code} />
                                }

                            </>)
                        }
                    </div>
                </FormGroup>

            </div>

        )
    }
}