import React, { useState, useEffect } from 'react'
import Axios from "axios";
import { apiSource } from "./Constants"
import Filter from './Filter';
import Table from './Table'
import CodeFilter from './CodeFilter';
import TablePagination from './TablePagination';
import NameFilter from './NameFilter';
import Nav from './Nav';
import Footer from './Footer';
import MySpinner from './MySpinner';
function Products() {
    const [pageSize, SetPageSize] = useState(10)
    const [pageNumber,] = useState("0")
    const [totalPages, SetTotalPages] = useState("")
    const [products, SetProducts] = useState([])
    const [mySpinner, SetMySpinner] = useState(true)



    useEffect(() => {
        console.log(apiSource)
        anyUpdate()
        //this.getDepartments()
        setFristQuery()

    }, [])
    function setFristQuery() {
        sessionStorage.setItem("depatmentId", "0")
        sessionStorage.setItem("code", "a")
        sessionStorage.setItem("product", "__")
    }

    function anyUpdate(myPageNumber = pageNumber, myDepatmentId = "0", myCode = "a", productName = "__") {
        SetMySpinner(true)
        Axios.get(`${apiSource}products/${pageSize}/${myPageNumber}/${myDepatmentId}/${myCode}/${productName}`)
            .then(response => {

                SetProducts(response.data[0].value.productList)
                SetTotalPages(response.data[0].value.count)
                SetMySpinner(false)
                console.log(products)
                console.log(totalPages)
            })
            .catch(error => console.log(error.response))
    }
    return (
        <>
            <MySpinner mySpinner={mySpinner} />
            <Nav />
            <div className="container">
                <div className="side">
                    <NameFilter key="filter1" anyUpdate={anyUpdate} />
                    <Filter key="filter2" anyUpdate={anyUpdate} />
                    <CodeFilter key="filter3" anyUpdate={anyUpdate} />
                </div>
                <div className="main">
                    <Table products={products} />
                    <TablePagination totalPages={totalPages} anyUpdate={anyUpdate} />
                </div>
            </div>
            <Footer />
        </>
    )
}
export default Products
