import React, { Component } from 'react'
import { Table } from 'reactstrap';

export default class Filter extends Component {


    render() {
        let { products } = this.props
        return (
            <div >
                <Table>
                    <thead>
                        <tr>
                            <th>Product Name</th>
                            <th>Price</th>
                            <th>Discount</th>
                            <th>Code</th>
                            <th>Price After Discount</th>
                        </tr>
                    </thead>
                    <tbody>

                        {products.map(product => (
                            <tr key={product.productId}>
                                <td>{product.name}</td>
                                {product.active === 1 ? <td style={{ textDecoration: "line-through", color: "red" }}> {product.price}</td> : <td> {product.price}</td>}
                                {product.active === 1 ? <><td>{product.discount}%</td><td> {product.code}</td><td style={{ color: "green" }}>{product.price * (100 - product.discount) / 100}</td></> : <></>}</tr>
                        ))}
                    </tbody>
                </Table>
            </div>

        )
    }
}