import React from 'react';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';

function TablePagination(props) {
    let { anyUpdate, totalPages } = props
    return (
        <>
            <Pagination style={{marginBottom:"10px"}} aria-label="Page navigation example">

                {[...Array(totalPages + 1)].map((e, i) => <PaginationItem key={i} >
                    <PaginationLink key={i} onClick={() => anyUpdate(i, sessionStorage.getItem("depatmentId"), sessionStorage.getItem("code"),sessionStorage.getItem("product"))}>
                        {i + 1}
                    </PaginationLink>
                </PaginationItem>)}
            </Pagination>
        </>
    );
}

export default TablePagination;
