import React, { Component } from 'react'
import { /* ListGroup, ListGroupItem, */ FormGroup, Label, CustomInput } from 'reactstrap';
import Axios from "axios";
import { apiSource } from "./Constants"
export default class Filter extends Component {
    state = {
        data: []
    }
    componentDidMount() {
        this.getData()
    }
    getData = () => {
        Axios.get(`${apiSource}departments`)
            .then(response => {
                this.setState({ data: response.data })
                console.log("data", this.state.data)
            })
            .catch(error => console.log(error.response))
    }
    onChange = (e) => {
        console.log(e.target.value)
        sessionStorage.setItem("depatmentId",e.target.value)
        this.props.anyUpdate(0,e.target.value,sessionStorage.getItem("code"),sessionStorage.getItem("product"))
    }

    render() {
        let { data } = this.state
        return (
            <div >
                <FormGroup>
                    <Label for="exampleCheckbox">Departments Filter</Label>
                    <div>
                    <CustomInput key="frist" type="radio" id="fristid" name="radio" label="All" onChange={ this.onChange} value="0" />

                        {
                            
                            data.map(item => <>
                                <CustomInput key={item.departmentId} type="radio" id={item.departmentId} name="radio" label={item.name_department} onChange={ this.onChange} value={item.departmentId} />

                            </>)
                        }
                    </div>
                </FormGroup>

            </div>

        )
    }
}